import React, { Component } from 'react';
import request from 'superagent';

class LastUser extends Component{
  constructor(){
    super();
    this.state = {
      country: '',
      firstName: '',
      lastName: ''
    };
  }

  componentDidMount(){
    request
      .get('/api/lastuser')
      .end((err, res)=>{

        var data = res.body;
        this.setState({
          country: data.country,
          firstName: data.firstName,
          lastName: data.lastName
        });
        
      });
  }

  render(){
    return(
      <div className="col-sm-3 col-lg-3">
        <div className="half-unit">
            <dtitle>Last Registered User</dtitle>
            <hr/>
              <div className="cont2">
                <img src="/img/user-avatar.jpg" alt=""/>
                <br/>
                <br/>
                <p>{this.state.firstName} {this.state.lastName}</p>
                <p><bold>Liverpool, {this.state.country}</bold></p>
              </div>
        </div>
        <div className="half-unit">
            <dtitle>Modal</dtitle>
            <hr/>
            <div className="cont">
              <a href="#myModal" role="button" className="btnnew" data-toggle="modal">Example Modal Window</a>
            </div>
        </div>
      </div>
    );
  }
}

export default LastUser;



